import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Quote from "components/Typography/Quote.js";

import image from "assets/img/faces/avatar.jpg";

import styles from "assets/jss/material-kit-react/views/componentsSections/typographyStyle.js";

const useStyles = makeStyles(styles);

export default function SectionTypography() {
  const classes = useStyles();
  const localUserStyles = makeStyles(theme => ({
    image: {
      width: "100%",
      marginBottom: "15px",
    },
    avatar: {
      width: "90%",
      borderRadius: '100%',
      "@media (max-width: 768px)": {
        width: "100%",
      }
    },
  }));
  const localclasses = localUserStyles()
  return (
    <div className={classes.section}>
      <div className={classes.container}>
        <div id="typography">
          <div className={classes.title}>
            <h2>Nos Contacts</h2>
          </div>
          <GridContainer>      
              <GridItem xs={12} sm={12} md={6} lg={6}>
              <div className={classes.typo}>
                <div className={classes.note}>Quote</div>
                <Quote
                text="Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolor culpa qui voluptate sit at incidunt a! Ex fugit nihil asperiores."
                author=" Kanye West, Musician"
                />
              </div>
            </GridItem>
          </GridContainer>
      </div>
        </div>
    </div>
  );
}
