import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons
import DriveEtaRoundedIcon from '@material-ui/icons/DriveEtaRounded';
import NewReleasesRoundedIcon from '@material-ui/icons/NewReleasesRounded';
import CardGiftcardIcon from '@material-ui/icons/CardGiftcard';
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Grid from '@material-ui/core/Grid';
import CustomTabs from "components/CustomTabs/CustomTabs.js";
import Typography from '@material-ui/core/Typography';

// core components
import Card from "components/Card/Card.js";
import { Link } from "react-router-dom";
/* 
import styles from "assets/jss/material-kit-react/components/customTabsStyle.js"; */
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from "components/CustomButtons/Button.js";
import Close from "@material-ui/icons/Close";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import IconButton from "@material-ui/core/IconButton";
import LibraryBooks from "@material-ui/icons/LibraryBooks";
import Slide from "@material-ui/core/Slide";
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';
import DoneAllRoundedIcon from '@material-ui/icons/DoneAllRounded';
import AcUnitRoundedIcon from '@material-ui/icons/AcUnitRounded';
import EuroSymbolRoundedIcon from '@material-ui/icons/EuroSymbolRounded';

import Skeleton from '@material-ui/lab/Skeleton';

import carImage from 'assets/img/toyota-rav4-5d-schwarz-2019.png';
import axiosInstance from '../../../axios.js';

import styles from "assets/jss/material-kit-react/views/componentsSections/tabsStyle.js";
import s from "assets/jss/material-kit-react/views/componentsSections/javascriptStyles.js";
const u = makeStyles(s);

const useStyles = makeStyles(styles);

export default function SectionTabs(props) {
  const classes = useStyles();
  const [cars, setCars] = React.useState([]);

  const {baseURL} = props;
  React.useEffect(() => {
		axiosInstance.get(`${baseURL}client-api/car-list/`).then((res) => {
			const allPosts = res.data;
      setCars(allPosts);
		});
	}, [baseURL]);

  return (
    <div className={classes.section}>
      <div className={classes.container}>
          <div className={classes.title}>
          <h2>Profitez de nos offres</h2>
          </div>
        <div id="nav-tabs">
          <GridContainer>
            <GridItem xs={12} sm={12} md={12}>
              <CustomTabs
                headerColor="primary"
                tabs={[
                  {
                    tabName: "Promos",
                    tabIcon: CardGiftcardIcon, 
                    tabContent: (
                      <div className={classes.textCenter}>
                        <Show cars={cars} tab={'promo'}/>
                      </div>
                    )
                  },
                  {
                    tabName: "Premium",
                    tabIcon: NewReleasesRoundedIcon,
                    tabContent: (
                      <div className={classes.textCenter}>
                        <Show cars={cars} tab={'premium'}/>
                      </div>
                    )
                  }
                ]}
              />
            </GridItem>
          </GridContainer>
        </div>
      </div>
    </div>
  );
}


const carStyles = makeStyles((theme) => ({
  root: {
    margin: '30px auto'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
  },
  cover: {
    minHeight: 160,
    width: '100%',
  },
  controls: {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'center'
  },
  para:{
    marginBottom: '20px',
  }, 
  b: {
    fontWeight: 'bold',
    color: 'black'
  }
}));
function Show({tab, cars}) {
  const classes = carStyles();
  function ShowCar({car}) {
    return (<Paper elevation={0} variant="outlined" className={classes.root}>
      <GridContainer>
        <GridItem xs={12} sm={12} md={12} lg={12}>
          <GridContainer  style={{margin: '30px 0 20px'}}>
            <GridItem xs={12} sm={12} md={6} lg={6}>
              <img src={carImage} alt={car.name} title={car.name} style={{maxWidth: '100%'}}/>
            </GridItem>
            <GridItem xs={12} sm={12} md={6} lg={6}>
          <div className={classes.details}>
            <CardContent className={classes.content}>
              <Typography component="h5" variant="h5">
                {car.name}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                {car.price}<sup>$</sup>/jour
              </Typography>
        </CardContent>
      </div>
      </GridItem>
          </GridContainer>
      </GridItem>
      <GridItem xs={12} sm={12} md={12} lg={12}>
      <Divider />
      <div className={classes.controls}>
          <Modal car={car}/>
          <Link to={`/commande/${car.id}`}> 
            <Button color="rose" className={classes.b}>reserver</Button>
          </Link>
        </div>
      </GridItem>
      </GridContainer>
    </Paper>)
  }
  const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="down" ref={ref} {...props} />;
  });
  
  Transition.displayName = "Transition";
  function Modal({car}){
    const [classicModal, setClassicModal] = React.useState(false);
    const classes = u();
    const useStyles = makeStyles(theme => ({
      reserver: {
        fontWeight: 'bold',
        color: 'black',
      },
    }));
    const localClasses = useStyles();
    return ( 
        <GridContainer>
          <GridItem xs={12} sm={12} md={6} lg={4}>
            <Button style={{border: '1px solid #27A', marginRight: 10, fontWeight: 'bold'}}  
              color="primary"
              simple
              onClick={() => setClassicModal(true)}
            >
              <LibraryBooks className={classes.icon} />
              Détails
            </Button>
            <Dialog
              classes={{
                root: classes.center,
                paper: classes.modal
              }}
              open={classicModal}
              TransitionComponent={Transition}
              keepMounted
              onClose={() => setClassicModal(false)}
              aria-labelledby="classic-modal-slide-title"
              aria-describedby="classic-modal-slide-description"
            >
              <DialogTitle
                id="classic-modal-slide-title"
                disableTypography
                className={classes.modalHeader}
              >
                <IconButton
                  className={classes.modalCloseButton}
                  key="close"
                  aria-label="Close"
                  color="inherit"
                  onClick={() => setClassicModal(false)}
                >
                  <Close className={classes.modalClose} />
                </IconButton>
                <h4 className={classes.modalTitle}>{car.name}</h4>
              </DialogTitle>
              <DialogContent
                id="classic-modal-slide-description"
                className={classes.modalBody}
              >
              <GridContainer direction="row" justify="space-around" alignItems="center">
                <GridItem xs={12} sm={12} md={4} lg={4}>
                  <img src={car.image} alt="car" style={{maxWidth: '100%'}} title={car.name}/>
                </GridItem>
                <GridItem xs={12} sm={12} md={8} lg={8}>
                  <div className={classes.details}>
                    <CardContent className={classes.content}>
                      <GridContainer direction="row" justify="flex-end">
                        <GridItem xs={12} sm={12} md={6} lg={6}>
                          {car.description ?<Typography>{car.description}</Typography> : null}
                          <Typography variant="subtitle1" color="secondary">
                            <div>{car.price} <sup>$</sup>/jour</div>
                          </Typography>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={6} lg={6}>
                          {car.climatisation ?<Typography  color="textSecondary">
                            <AcUnitRoundedIcon color='primary'/> Climatisation
                            </Typography> : null}
                          {car.boite ? <Typography color="textSecondary">
                            <DoneAllRoundedIcon color='primary'/> Boite automatique
                            </Typography> : null}
                          <Divider />
                          <GridContainer direction="row" justify="flex-end">
                            <GridItem xs={6} sm={6} md={6} lg={6}>
                              <Typography color="textSecondary">{car.porte} Portes </Typography>
                              <Typography color="textSecondary">{car.place} Places</Typography>
                            </GridItem>
                            <GridItem xs={6} sm={6} md={6} lg={6}>
                              <Typography color="textSecondary">{car.sac} Sacs</Typography>
                              <Typography color="textSecondary">{car.valise} valises </Typography>
                            </GridItem>
                          </GridContainer>
                        </GridItem>
                      </GridContainer>
                    </CardContent>
                  </div>
                </GridItem>
                <GridItem  xs={12} sm={12} md={12} lg={12}>
                <Divider />
              </GridItem>
            </GridContainer>
          </DialogContent>
          <DialogActions className={classes.modalFooter}>
                <Button
                  onClick={() => setClassicModal(false)}
                  color="danger"
                  simple
                >
                  Fermer
                </Button>
          <Link to={`/commande/${car.id}`}> 
                <Button color="rose" className={localClasses.reserver}>
                  Reserver
                </Button>
          </Link>
              </DialogActions>
            </Dialog>
          </GridItem>
        </GridContainer>
    )
  }
  
    return(<div>
        <Typography variant="h6">{tab}</Typography>
        <Typography  component="p" className={classes.para}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Error, totam.</Typography>
        <GridContainer>
        { cars.length ? cars.map( (car, key)=>{
            return tab === 'premium' ? 
            car.premium ? <GridItem key={key}  xs={12} sm={6} md={6}> <ShowCar car={car}/> </GridItem>: null : 
            car.promo ? <GridItem key={key}  xs={12} sm={6} md={6}><ShowCar car={car}/></GridItem>  : null
        }) : 
        <GridItem  xs={12} sm={12} md={12}>
          <Skeleton animation="wave" variant="rect" width={`${100}%`} height={`${30}vh`}  />
        </GridItem>}
        </GridContainer>
      </div>)   
}